@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';
@import '../../__data__/style/colors.module.css';

.basketProductsCard {
    display: flex;
    justify-content: space-between;
    padding: var(--offset-md) 0;
    width: 100%;
    align-items: center;
    border-bottom: 1px solid var(--black);
}

.basketProductsView {
    display: flex;
    justify-content: space-between;
    flex: 0 0 416px;
    align-items: center;
    cursor: pointer;
    color: var(--black);
}

.basketProductsImg {
    flex: 0 0 100px;
    height: 100px;
    position: relative;
}

.basketProductsImg img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: contain;
}

.basketProductsDescription {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0px;
    flex: 0 0 300px;
    margin-left: var(--offset-sm);
}

.basketProductsCounting {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: var(--offset-md);
}

.basketProductsCount {
    max-width: 140px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.basketProductsCount strong {
    font-size: 22px;
    line-height: 28px;
    letter-spacing: 0px;
    font-weight: bold;
    padding: 0 var(--offset-xs);
}

.basketProductsPrice {
    letter-spacing: 0px;
}

.basketProductsPrice strong {
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
}

.basketProductsPrice small {
    margin-left: var(--offset-xs);
    font-size: 18px;
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .basketProductsView {
        flex: 0 0 376px;
    }
    .basketProductsDescription {
        flex: 0 0 250px;
        font-size: 18px;
        line-height: 22px;
    }
    .basketProductsCounting {
        margin-left: var(--offset-sm);
    }
    .basketProductsCount strong {
        font-size: 20px;
        line-height: 24px;
    }
    .basketProductsPrice strong {
        font-size: 20px;
        line-height: 24px;
    }
}

@media screen and (max-width: 731.98px) {
    .basketProductsView {
        flex: 0 0 auto;
        width: 100%;
    }

    .basketProductsCard {
        flex-direction: column;
    }

    .basketProductsDescription {
        flex: 1 1 auto;
        font-size: 14px;
        line-height: 18px;
    }
    .basketProductsCounting {
        margin-top: var(--offset-sm);
        width: 100%;
        margin-left: 0;
    }
    .basketProductsCount {
        max-width: 130px;
    }
    .basketProductsCount strong {
        font-size: 18px;
        line-height: 22px;
        padding: 0;
    }
    .basketProductsPrice strong {
        font-size: 18px;
        line-height: 22px;
    }
}
