@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';
@import '../../__data__/style/colors.module.css';

.basket {
    background-color: var(--white);
    color: var(--black);
}

.basketBody {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.basketTitle {
    font-size: 24px;
    line-height: 30px;
    font-weight: bold;
    margin-left: var(--offset-xs);
    display: inline-block;
}

.basketText {
    margin-top: var(--offset-lg);
    font-size: 18px;
    line-height: 22px;
}

.basketLink {
    margin-top: var(--offset-lg);
    font-size: 18px;
    line-height: 22px;
    color: var(--green-4);
}

.basketLink:hover {
    text-decoration: underline;
}

.basketLink:focus {
    outline-color: var(--green-4);
}

.basketToMainLink {
    margin-top: var(--offset-lg);
    max-width: 350px;
    width: 100%;
}

.basketProductsTitle {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    align-items: center;
}

.basketProductsSum {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    margin-top: var(--offset-md);
}

.basketProductsQuantity {
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0px;
}

.basketProductsQuantity span {
    font-weight: normal;
}

.basketProductsQuantity strong {
    margin-left: var(--offset-sm);
    font-weight: bold;
}

.basketProductsTotalPrice span {
    font-size: 22px;
}

.basketProductsTotalPrice strong {
    font-weight: 600;
    font-size: 24px;
    margin-left: var(--offset-md);
}

.basketProductsTotalPrice small {
    margin-left: var(--offset-xs);
    font-size: 18px;
}

.basketProductsBtns {
    margin-top: var(--offset-lg);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.basketProductsToMainLink {
    min-width: 180px;
    min-height: 40px;
    border-radius: 3px;
    letter-spacing: 1.05px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    padding: 10px 0;
    background-color: var(--green-2);
    color: var(--green-4);
}

.basketProductsToMainLink:hover {
    outline: 1px solid var(--green-4);
    box-shadow: 0 0 3pt 2pt var(--green-4);
}

.basketProductsToMainLink:focus {
    outline: 1px solid var(--green-4);
    box-shadow: 0 0 3pt 2pt var(--green-4);
}

.basketProductsPay {
    min-width: 180px;
}

.container {
    max-width: 1240px;
    margin: 0 auto;
    padding: var(--offset-lg) var(--offset-sm);
    padding-top: 132px;
}

.buttonOffset {
    width: 100%;
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .container {
        padding: var(--offset-md2) var(--offset-sm);
        padding-top: 104px;
    }
}

@media screen and (max-width: 731.98px) {
    .basketProductsSum {
        flex-direction: column;
    }
    .basketProductsQuantity strong {
        margin-left: var(--offset-xs);
    }

    .basketProductsTotalPrice {
        margin-top: var(--offset-md);
    }

    .basketProductsTotalPrice strong {
        font-size: 20px;
        margin-left: var(--offset-xs);
    }

    .basketProductsBtns {
        margin-top: var(--offset-md);
        flex-direction: column;
        max-width: 350px;
    }

    .basketProductsToMainLink {
        order: 2;
        width: 100%;
        margin-top: var(--offset-md);
    }

    .basketProductsPay {
        order: 1;
        width: 100%;
    }

    .container {
        padding-top: 84px;
    }
}
